<template>
    <v-card class="px-10">
        <CloseButton v-on:close="$emit('close')"/>

        <v-card-title>{{title}}</v-card-title>

        <v-subheader v-if="info.length > 0">Info</v-subheader>
        <v-card-text v-if="info.length > 0">{{info}}</v-card-text>

        <v-subheader>Histrico</v-subheader>

        <Graph :type="type" :reservoir="reservoir" />

        <v-btn
            absolute
            right
            :style="{bottom: '50%', transform:'translateY(-50%)'}"
            icon
            :disabled="type == 'vol'"
            @click="$emit('next', type)"
        >
                <v-icon>fas fa-arrow-right</v-icon>
        </v-btn>

        <v-btn
            absolute
            left
            :style="{bottom: '50%', transform:'translateY(-50%)'}"
            icon
            :disabled="type == 'wqi'"
            @click="$emit('back', type)"
        >
                <v-icon>fas fa-arrow-left</v-icon>
        </v-btn>
    </v-card>
</template>

<script>
import CloseButton from './CloseButton'
import Graph from './Graph'

export default {
    data: () => ({
        title: '',
        info: ''
    }),
    props: {
        type: String,
        reservoir: Number
    },
    components: {
        CloseButton,
        Graph
    },
    watch: {
        type(){
            this.update_data()
        }
    },
    methods: {
        update_data(){
            if(this.type == 'wqi'){
                this.title = 'IQA'
                this.info = 'O ndice de Qualidade das guas (IQA)  o principal indicador qualitativo usado no pas. Foi desenvolvido para avaliar a qualidade da gua para o abastecimento pblico, aps o tratamento convencional.'
            }
            else if(this.type == 'temp'){
                this.title = 'Temperatura'
                this.info = 'Variaes de temperatura so parte do regime climtico normal dependendo da altitude, latitude, estao do ano, perodo do dia, taxa de fluxo e profundidade. Em geral,  medida que a temperatura aumenta, de 0 a 30C, alguns fatores diminuem como a viscosidade, tenso superficial, compressibilidade, calor especfico, constante de ionizao e calor latente de vaporizao, enquanto a condutividade eltrica e trmica, a presso de vapor, a solubilidade de sais aumentam com a elevao da temperatura.'
            }
            else if(this.type == 'ph'){
                this.title = 'pH'
                this.info = ' usado para indicar o quo cida ou bsica est a gua, ou seja, expressa a concentrao de ons de hidrognio no lquido. Alteraes nos valores de pH tambm podem aumentar o efeito de substncias qumicas que so txicas para os organismos aquticos, tais como os metais pesados.'
            }
            else if(this.type == 'do'){
                this.title = 'Oxignio Dissolvido'
                this.info = 'As guas poludas por esgotos apresentam baixa concentrao de oxignio dissolvido pois o mesmo  consumido no processo de decomposio da matria orgnica. Por outro lado as guas limpas apresentam concentraes de oxignio dissolvido mais elevadas.'
            }
            else if(this.type == 'ec'){
                this.title = 'Condutividade Eltrica'
                this.info = ' a capacidade que a gua possui de conduzir corrente eltrica.  uma medida da concentrao total de sais dissolvidos presentes na gua. Apesar de no fornecer medidas reais da concentrao de um determinado on presente, ela nos d uma noo muito boa do quanto a gua est impactada, o que indiretamente sugere a origem e o grau de contaminao da gua.'
            }
            else if(this.type == 'tds'){
                this.title = 'Totais de Slidos Dissolvidos.'
                this.info = 'Os totais slidos dissolvidos (TSD) compreendem sais inorgnicos (principalmente clcio, magnsio, potssio, sdio, bicarbonatos, cloretos e sulfatos) e algumas pequenas quantidades de matria orgnica que so dissolvidas em gua.'
            }
            else if(this.type == 'sal'){
                this.title = 'Salinidade'
                this.info = 'Salinidade  uma medida da quantidade de sais existentes em massas de gua naturais. A forma mais simples de descrever a salinidade  como a relao entre o contedo de sais dissolvidos em uma dada quantidade de gua.'
            }
            else if(this.type == 'turb'){
                this.title = 'Turbidez'
                this.info = 'A Turbidez da gua  causada pela presena de partculas em suspenso ou materiais como argilas, terra, areia, plncton, xidos, hidrxidos e carbonatos minerais.'
            }
            else if(this.type == 'vol'){
                this.title = 'Volume'
                this.info = ''
            }
            else if(this.type == 'cons'){
                this.title = 'Consumo'
                this.info = ''
            }
            else if(this.type == 'pred'){
                this.title = 'Predio'
                this.info = ''
            }
        }
    },
    mounted(){
        this.update_data()
    }
}
</script>